<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
import md5 from 'md5'
// import socket from "@/plugins/socket.io.js";
var id = require("date-fns/locale/id");
export default {
  components: {
  },
  data () {
    return {
        notifs:[],
      classMenu:"col-sm-12",
      notifn:0,
      count:0,
      bgleft1: '#343A40',
      bgleft2: '#232e30',
      bgleft3: '#343A40',
      bgrighttop: '#1A3B4E',
      test: 1,
      menuTrigger: false,
      menuKanan: false,
      bearer: '',
      busy: false // digunakan untuk melihat apakah ada data yang masih diprocess untuk dimunculkan loading
    }
  },
   beforeMount() {
      let that = this;
      if(!this.$store.state.users){ //atau tanpa ini
        this.$store.dispatch('ceklogin',{//firebase
          true:true,
          false:'/'
        }) 
      // this.$store.dispatch('ceklogin2');//php auto
      }
    //   socket.on("new-message", message => {
    //   if(message.target_id==that.$store.state.users.id && message.app=='driver'){
    //       that.refreshNotif()
    //        //  CORDOVA
    //       document.addEventListener('deviceready', function () {
    //           cordova.plugins.notification.local.schedule({
    //               title: 'BTN Banjarbaru',
    //               text: message.text,
    //               sound:'file://resources/audio/beep.mp3'
    //           });
    //           cordova.plugins.notification.local.on('click', function (notification, eopts) { 
    //               setTimeout(() => {
    //                   that.$router.push(message.link)
    //               }, 1000);
    //           });
    //           navigator.notification.beep(1);
    //       })
    //   }
    // });
  },
  watch:{
      menuTrigger(){
          console.log(this.menuTrigger)
          if(this.menuTrigger){
              setTimeout(() => {
                  this.classMenu='col-sm-10'
              }, 300);
          }else{
              this.classMenu='col-sm-12'
          }
      }
  },
  methods: {
    cek(){
      if(localStorage.getItem('notifid')){
          db.collection(`users`).doc(this.$store.state.users.uid).set({fcm:localStorage.getItem('notifid')},{merge:true}).then(res=>{
            // alert("Berhasil di simpan notifikasi ke database")
        })
      }
    },
    exits(){
      document.addEventListener("deviceready", function(e){
        navigator.app.exitApp();
      }, true);
    },  
     distanceToNow(tanggal) {
          return this.$datefns.distanceInWordsToNow(
            new Date(tanggal),
            {
              locale: id,
              includeSeconds: true
            }
          );
        },
    logout () {
      this.$store.dispatch('logout')
      localStorage.removeItem('login')
      this.$router.push('/')
    },
    muncul (e) {
      e.target.nextElementSibling.classList.toggle('isaktif')
    },
    onSwipeRight () {
      this.menuTrigger = true
    },
    scrollOnLoad () {
      this.$el.querySelectorAll('a.scrollactive-item').forEach(el => {
        if (el.hash == this.$route.hash) {
          el.click()
        }
      })
    },
    version () {
      if (localStorage.getItem('version') == null) {
        localStorage.setItem('version', 1)
      }
    },
    newStuff () {
      if (localStorage.getItem('newstuff') != this.$store.state.version) {
        var req = indexedDB.deleteDatabase(this.$store.state.indexdb)
        req.onsuccess = function () {
          console.log('Deleted database successfully')
        }
        req.onerror = function () {
          console.log("Couldn't delete database")
        }
        req.onblocked = function () {
          console.log(
            "Couldn't delete database due to the operation being blocked"
          )
        }
        localStorage.clear()
        localStorage.setItem('newstuff', this.$store.state.version)
        alert('new version')
      }
    },
    mediaQueries () {
      this.$store.commit(
        'mediaQueries',
        window.innerWidth <= 576
          ? 'sm'
          : window.innerWidth <= 768
            ? 'md'
            : window.innerWidth <= 992
              ? 'lg'
              : 'xl'
      )
    },
    init () {
      this.scrollOnLoad()
      // this.cekAuth();
      this.version()
      //! mencek apakah expireData berakhir
      // this.$store.dispatch('auth/autoLogout');
    }
  },
  mounted () {
    let that = this;
    db.collection('notifikasi').onSnapshot(res=>{
      let data = res.docs.map(e=>{
        return {
          id:e.id,
          ...e.data()
        }
      })
      this.notifs=data
    })
    setTimeout(() => {
      this.cek()
    }, 5000);
    function idleLogout() {
      var t;
      window.onload = resetTimer;
      window.onmousemove = resetTimer;
      window.onmousedown = resetTimer; // catches touchscreen presses as well
      window.ontouchstart = resetTimer; // catches touchscreen swipes as well
      window.onclick = resetTimer; // catches touchpad clicks as well
      window.onkeypress = resetTimer;
      window.addEventListener("scroll", resetTimer, true); // improved; see comments
      function yourFunction() {
        alert('idle')
        // your function for too long inactivity goes here
        // e.g. window.location.href = 'logout.php';
      }

      function resetTimer() {
        clearTimeout(t);
        console.log('idle di reset')
        t = setTimeout(yourFunction, 10000); // time is in milliseconds
      }
    }
    // idleLogout();
    this.mediaQueries()
    this.$nextTick(() => {
      window.addEventListener('resize', () => {
        that.mediaQueries()
      })
    })
    this.init()
    setTimeout(() => {
      this.$store.commit('nossr', true)
    }, 100)
    this.$nuxt.$on('busy', data => {
      this.busy = data
    })
    // console.log(this.$store.state.users.email)
    if(this.$store.state.users){
        if(this.$store.state.users.email.indexOf('admin@gmail')!=-1){
          this.$router.push('/admin')
      }
      }
    this.$store.dispatch('ceklogin',{
      true:true,
      false:'/auth/loginadmin'
    })
    document.addEventListener(
      "deviceready",
      function() {
        var push = PushNotification.init({
          android: {
            senderID: "668836186627", // setting project firebase
            sound: "true",
            vibrate: "true",
            clearNotifications: true, // clear notifications from shade on app start
            forceShow: true
          },
          ios: {
            alert: "true",
            badge: "true",
            sound: "true"
          }
        });
        PushNotification.createChannel(() => {
          console.log('success');
        }, () => {
          console.log('error');
        }, {
          id: "alarm",
          description: "My first test channel",
          importance: 5,
        vibration: [500, 1000, 500, 500],
          sound:'test', // test adalah test.wav nama file sound notification
        });
        push.on("registration", function(data) {
          // ! AMBIL data registrationId untuk ditaruh sebagai payload to : ""
          // ! simpan data regitrationId kedatabase
          localStorage.setItem("notifid", data.registrationId);
          that.regis = data.registrationId;
          that.$forceUpdate()
          if(that.$store.state.users.uid){
        
          }
          // Simpan data.registrationId KE DATABASE DENGAN GANDENGAN ID USER untuk di tembak dengan payload {to : ""} firebase
        });
        push.subscribe(
          'gofbuy',
          () => {
            console.log('success');
          },
          e => {
            console.log('error:', e);
          }
        );
        push.on("notification", function(data) {});
        push.on("error", function(e) {
          console.log(e.message);
        });
      },
      false
    );
  },
}
</script>
